.navbar {
    background: #ffffff;
    height: 80px;
    display: flex;
    /* justify-content: center; */
    justify-content: space-around;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    /* color:rgb(14, 121, 10); */
    background-color:#ffffff;
  }

